<template>
  <div class='item'>
    <el-card shadow="hover" class='card'>
      <div class='content'>
        <div class='left'>
          <div class='top'>
            {{typeFormatted[0]}} {{nameFormatted}}
          </div>

          <div class='middle'>
            {{descFormatted}}
          </div>

          <div class='footer'>
            <span>
              {{schoolFormatted}}
            </span>

            <span class='time'>
              {{typeFormatted[1]}} {{timeFormatted}}
            </span>
          </div>
        </div>

        <div class='picture' v-if='picture'>
          <el-image :src="pictureFormatted" fit='fill'></el-image>
        </div>
      </div>

      <div class='actions'>
        <span class='open' @click='showLafDetail()'>
          <span>{{$t('Laf.DetailOpen')}}</span>
          <i class='el-icon-caret-bottom'></i>
        </span>
      </div>

    </el-card>
  </div>
</template>

<script>
import * as server from '../../../server.js'

export default {
  name: 'laf-item',
  props: {
    lafID: { type: Number },
    type: { type: Number },
    name: { type: String },
    desc: { type: String },
    school: { type: Number },
    location: { type: String },
    thatTime: { type: String },
    userID: { type: Number },
    picture: { type: String },
  },
  computed: {
    nameFormatted () {
      if (this.name && this.name.length > 6) {
        return this.name.substring(0, 6) + '...';
      }
      return this.name;
    },
    descFormatted () {
      if (this.desc && this.desc.length > 16) {
        return this.desc.substring(0, 16) + '......';
      }
      return this.desc;
    },
    schoolFormatted () {
      return this.formatSchool(this.school)
    },
    typeFormatted () {
      return this.type == '0' ? [this.$t('Laf.DetailFound'), this.$t('Laf.ItemFoundAt')] : [this.$t('Laf.DetailLost'), this.$t('Laf.ItemLostAt')];
    },
    timeFormatted () {
      return this.formatTime(this.thatTime);
    },
    pictureFormatted () {
      return server.default.server + this.picture;
    }
  },
  methods: {
    showLafDetail () {
      this.$bus.$emit('showLafDetail', this.lafID);
    }
  }
}
</script>

<style scoped>
.item {
  margin-left: 10%;
  width: 80%;
}
.card {
  border-radius: 20px;
}
.content {
  display: flex;
  justify-content: space-around;
}
.left {
  text-align: left;
  margin-left: 15px;
  width: 100%;
}
.top {
  font-size: 30px;
  margin-top: 10px;
}
.middle {
  font-size: 20px;
  margin-top: 20px;
}
.footer {
  font-size: 20px;
  margin-top: 20px;
}
.time {
  margin-left: 20px;
}
.open {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
  cursor: pointer;
  color: rgb(61, 87, 235);
}
.picture .el-image {
  width: 130px;
  height: 150px;
}
</style>